.cartitem {
  width: 100%;
  padding: 1rem;
  margin-bottom: 8px;
  display: grid;
  grid-template-columns: 1fr 4fr 1fr 1fr 1fr;
  gap: 8px;
  background: #fff;
  border-radius: 2px;
  place-items: center;
}

.cartitem_name {
  text-decoration: none;
  color: #171717;
}

.cartitem_name:hover {
  color: #dd219e;
}

.cardItem__select {
  padding: 10px 17px;
}

.cartitem__deleteBtn {
  padding: 10px 17px;
  color: red;
  background: #f4f4f4;
  cursor: pointer;
  border: 1px solid #171717;
  transition: all 0.3s ease-out;
}

.cartitem__deleteBtn:hover {
  background: #171717;
  transform: scale(1.2);
}

@media (max-width: 700px) {
  .cartitem_name {
    font-size: 0.8rem;
  }
  .cardItem__select,
  .cartitem__deleteBtn {
    padding: 10px 13px;
  }
  .cartitem__price {
    font-size: 0.8rem;
  }
}

@media (max-width: 500px) {
  .cartitem_name {
    font-size: 0.6rem;
  }
  .cardItem__select,
  .cartitem__deleteBtn {
    padding: 5px 8px;
  }
  .cartitem__price {
    font-size: 0.6rem;
  }
}
